<template>
  <div class="page">
    <!-- MButton -->
    <h2>Vue Components :</h2>
    <p>
      m-labs provides you with Pre-built components that adapts its color
      engine, which results in an automatic theming mechanism with your chosen
      colors and settings.
    </p>
    <p>Now watch how all the m-components adapts your seetings :</p>
    <m-button @click="changeRadius()">Try changing UI radius</m-button>
    <m-button @click="changeAccent()">Try changing accent color</m-button>
    <p>
      for more editing by the user, every component has a special class with its
      name but in capital letters. For example you can customize the MButton
      component with the css class <m-code code=".MBUTTON" />
    </p>
    <article>
      <h3>1. MButton</h3>
      <p>
        Use the <m-code code="<m-button>" /> to display a pre-styled button with
        the following attributes :
      </p>
      <ol>
        <li>
          <m-code code="type" />: four pre-styled types of buttons (sucess,
          warning, danger, default).
        </li>
        <li>
          <m-code code="icon" />: to give your button an icon by adding the icon
          class in this attribute (as in font awesome or any similar library).
        </li>
      </ol>
      <m-button>default</m-button>
      <m-button type="success" icon="fas fa-circle-notch fa-spin"
        >success</m-button
      >
      <m-button type="warning">warning</m-button>
      <m-button type="danger">danger</m-button>
    </article>
    <article>
      <h3>2. MDText</h3>
      <p>
        A paragraph that switch direction automatically when it detects Arabic
        text. <br />
        Note: It supports <m-code code="contenteditable" /> attribute.
        <br />
        MDText now supports writing math formulas using Mathjax. include the
        attribute <m-code code="math='true'" />. Learn how to write mathjax from
        <a href="http://docs.mathjax.org/en/latest/">here</a> in order to use
        this feature.
        <br />
        Note that you first have to include th e mathjax script in your html
        file :
        <m-code
          code="<script src='https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.2/MathJax.js?config=TeX-AMS_HTML'></script>"
          type="block"
          language="Html"
        />
      </p>
      <h4>Demo :</h4>
      <m-code
        code="<m-d-text contenteditable='true' content='Hello World !' />"
        type="block"
        language="Vue"
      />
      <m-code
        code="<m-d-text content='بسم الله الرحمن الرحيم' />"
        type="block"
        language="Vue"
      />
      <m-code
        code="<m-d-text content='$$ x = \frac {-b \pm \sqrt \Delta}{2a} $$' />"
        type="block"
        language="Vue"
      />
      <m-d-text contenteditable="true" content="Hello World !" />
      <m-d-text math="true" content="بسم الله الرحمن الرحيم" />
      <m-d-text content="$$ x = \frac {-b \pm \sqrt \Delta}{2a} $$" />
    </article>
    <article>
      <h3>3. MNotes</h3>
      <p>
        Push the 4 types notifications with <m-code code="<m-notes/>" />.
        <br />
        All you have to do is to use the <m-code code="$refs" /> in vue to use
        the add function built in this component.
      </p>
      <h4>Demo :</h4>
      <m-code
        type="block"
        language="Vue"
        code="
<template>
  <m-button icon='fas fa-angle-double-right' @click='f'>
    Push Notification
  </m-button>
  <m-notes ref='mnotes'/>
</template>
<script>
  f() {
    this.$refs.mnotes.add({
      title: 'Hello World !',
      text: 'This component is so cool',
      type: 'sucess',
    });
  },
</script>

"
      />
      <m-button m="shadow" icon="fas fa-angle-double-right" @click="f">
        Push Notification
      </m-button>
      <m-notes ref="mnotes" />
    </article>
    <article>
      <h3>4. MCode</h3>
      <p>
        It's the component used to show codes in the page. There is two types of
        it: Block code &amp; inline code. <br />
        The default for this component is inline code while you can use the
        block code by using <m-code code="type='block'" />. <br />
        You can also add a small sign that shows the language used in your code
        by using the attribute <m-code code="language='Js'" /> for example (make
        sure you write the first letter in capital letter to get the right
        coloring).
      </p>
    </article>
    <article>
      <h3>5. MTabs</h3>
      <p>
        the MTabs components is the same component used in the top of this page
        where the links are.
        <br />
        use this component with the <m-code code="links" /> attribute to provide
        a stringified object of your vue router links where the key is the your
        links text and the value is the actual link.
      </p>
      <h4>Demo :</h4>
      <m-code
        type="block"
        language="Vue"
        code="
<m-tabs :links='
  JSON.stringify({
    Home: '/',
    'About us': '/about-us',
    Contact: '/contact',
  })
'/>
      "
      />
    </article>
    <article>
      <h3>6. MMenu <m-light text="New" /></h3>
      <p>
        A custom context menu (right click menu) to use in the full page or on a
        single element. Use the refs (as explaind earlier in MNotes component)
        to access the <m-code code="show($event)" /> and
        <m-code code="hide($event)" /> methods. The code below shows how to use
        it.
      </p>
      <m-code
        type="block"
        language="Vue"
        code="
<template>
  <p @contextmenu.prevent='$refs.menu.show($event)'>
    right click me
  </p>
  <transition name='fade-top'>
    <m-menu ref='menu'>
      <m-menu-item @click='doSth()' icon='fas fa-plus'>
        Do Something
      </m-menu-item>
      <m-menu-item
        type='danger'
        icon='fas fa-times'
        @click='cancel()'
      >
        Cancel
      </m-menu-item>
    </m-menu>
  </transition>
</template>
"
      />
    </article>
    <article>
      <h3>7. MMenuItem <m-light text="New" /></h3>
      <p>
        This component is made to be used as the items for the previous menu
        component. this component takes the same <m-code code="icon" /> and
        <m-code code="type" /> and they have the same purpose and values here
        too.
      </p>
    </article>
    <article>
      <h3>8. MNews <m-light text="New" /></h3>
      <p>
        The MNews component is used to attract eyes to some important text.
        <br />
        The box below is a good example
      </p>
    </article>
    <m-news>
      More components will be Added soon. Please feel free to contact the
      developer and ask or suggest new components.
    </m-news>
    <article>
      <h3>9. MPoppup <m-light text="New" /></h3>
      <p>
        a simple poppup that asks the user something or inform him with some
        information.
        <br />
        Start by importing the component and give it a
        <m-code code="ref" /> attribute so you can use its methods.
        <br />
        use the <m-code code="show({title, message, confirm, cancel})" /> method
        to show the poppup. The confirm and the cancel props can be a string
        that represents confirm and cancel buttons texts, and they can be an
        array to specify the button type too (see MButton).
        <br />
        This method return a promise where it returns true when the user presses
        the confirm button and false if the user presses cancel. However the
        cancel button is optional.
      </p>
      <m-code
        type="block"
        language="Vue"
        code="
<template>
  <m-poppup ref='pop'/>
  <m-button @click='showPop'>Click me</m-button>
</template>
<script>
  methods: {
    async showPop() {
      const answer = await this.$refs.pop.show({title: 'MPoppup test', message: 'Do you like this component ?', confirm: 'yes', cancel: 'no'});
      console.log(answer);
    }
  }
</script>
"
      />
      <m-button @click="showPop">Click me</m-button>
      <transition name="fade-back">
        <m-poppup ref="pop" />
      </transition>
    </article>
  </div>
</template>

<script>
import { setDefaultTheme, setTheme, configUI } from "m-labs";
export default {
  name: "Vue",
  methods: {
    f() {
      this.$refs.mnotes.add({
        title: "Hello World !",
        text: "This component is so cool",
        type: "success",
      });
    },
    async showPop() {
      const answer = await this.$refs.pop.show({
        title: "MPoppup test",
        message: "Do you like this component ?",
        confirm: "yes",
        cancel: "no",
      });
      console.log(answer);
    },
    changeAccent() {
      let colors = [
        "rgb(82, 0, 224)",
        "rgb(50, 198, 123)",
        "rgb(214, 208, 28)",
        "rgb(240, 63, 63)",
        "rgb(176, 93, 170)",
      ];
      setDefaultTheme({
        accent: `${colors[Math.floor(Math.random() * 5)]}`,
      });
      setTheme("dark", {
        accent: `${colors[Math.floor(Math.random() * 5)]}`,
      });
    },
    changeRadius() {
      configUI(
        { ar: "Cairo", en: "Poppins" },
        `${Math.floor(Math.random() * 20)}px`
      );
    },
  },
};
</script>

<style lang="scss" scoped>
.MDTEXT {
  background: rgba(0, 0, 0, 0.1);
}
</style>
